import React, { useEffect, useMemo, useRef, useState } from "react";

/**
 * Entraîneur de Multiplication Amusant (2025) — Profil + Modes + Correctifs
 *
 * ✅ Corrige l'erreur: randInt était utilisé mais non défini → défini ci‑dessous
 * ✅ Conserve: profil enfant, bascule son, mode (3 choix / saisie), confettis, design 2025
 * ✅ Ajoute: auto‑tests (dev) pour prévenir régressions (randInt, options, etc.)
 */

/******************** Utils & Storage ********************/ 
const LS_KEYS = {
  kidName: "multrainer_kid_name",
  mode: "multrainer_mode", // "Choix multiple" | "Saisie manuelle"
  mute: "multrainer_mute",
  streak: "multrainer_streak",
};

/** Random integer in [min, max] */
function randInt(min, max) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

function generatePair() {
  return { a: randInt(0, 9), b: randInt(0, 9) };
}

/**
 * Returns `count` wrong answers for a given correct value.
 * Ensures: unique, within [0, 81], and != correct.
 */
function pickWrongAnswers(correct, count = 2) {
  const chosen = new Set();
  const candidateDeltas = [-9, -5, -4, -3, -2, -1, 1, 2, 3, 4, 5, 8, 9];

  // First pass: near-correct candidates
  for (const d of candidateDeltas) {
    if (chosen.size >= count) break;
    const v = correct + d;
    if (v >= 0 && v <= 81 && v !== correct) chosen.add(v);
  }

  // Second pass: fill with randoms if needed
  while (chosen.size < count) {
    const v = randInt(0, 81);
    if (v !== correct) chosen.add(v);
  }

  return Array.from(chosen).slice(0, count);
}

function useLocalStorageState(key, defaultValue) {
  const [value, setValue] = useState(() => {
    try {
      const v = localStorage.getItem(key);
      return v !== null ? JSON.parse(v) : defaultValue;
    } catch {
      return defaultValue;
    }
  });
  useEffect(() => {
    try { localStorage.setItem(key, JSON.stringify(value)); } catch {}
  }, [key, value]);
  return [value, setValue];
}

/******************** UI Bits ********************/
function Confetti({ fire }) {
  const [pieces, setPieces] = useState([]);
  useEffect(() => {
    if (!fire) return;
    const emojis = ["🎉", "⭐", "🎈", "✨", "🎊", "👏"];
    const ps = Array.from({ length: 40 }).map((_, i) => ({
      id: i + "-c",
      x: Math.random() * 100,
      y: -10 - Math.random() * 20,
      rot: Math.random() * 360,
      vy: 0.6 + Math.random() * 1.2,
      vx: -0.5 + Math.random() * 1.0,
      emoji: emojis[randInt(0, emojis.length - 1)],
    }));
    setPieces(ps);

    let raf;
    const tick = () => {
      setPieces((prev) =>
        prev
          .map((p) => ({ ...p, x: p.x + p.vx, y: p.y + p.vy, rot: p.rot + 3 }))
          .filter((p) => p.y < 120)
      );
      raf = requestAnimationFrame(tick);
    };
    raf = requestAnimationFrame(tick);
    return () => cancelAnimationFrame(raf);
  }, [fire]);

  return (
    <div className="pointer-events-none fixed inset-0 z-50 overflow-hidden">
      {pieces.map((p) => (
        <div
          key={p.id}
          style={{ left: `${p.x}%`, top: `${p.y}%`, transform: `rotate(${p.rot}deg)` }}
          className="absolute text-3xl"
        >
          {p.emoji}
        </div>
      ))}
    </div>
  );
}

function ProgressBar({ value, max }) {
  const pct = Math.min(100, Math.round((value / max) * 100));
  return (
    <div className="w-full max-w-md rounded-full bg-blue-100 p-1">
      <div
        className="h-3 rounded-full transition-all duration-500"
        style={{ width: `${pct}%`, background: "linear-gradient(90deg,#2563eb,#10b981,#f59e0b)" }}
      />
    </div>
  );
}

function useSound(mute) {
  const correctRef = useRef(null);
  const wrongRef = useRef(null);
  useEffect(() => {
    const Ctx = window.AudioContext || window.webkitAudioContext;
    const ctx = Ctx ? new Ctx() : null;
    correctRef.current = () => {
      if (mute || !ctx || !ctx.createOscillator) return;
      const o = ctx.createOscillator();
      const g = ctx.createGain();
      o.connect(g); g.connect(ctx.destination);
      o.type = "sine"; o.frequency.setValueAtTime(880, ctx.currentTime);
      g.gain.setValueAtTime(0.001, ctx.currentTime);
      g.gain.exponentialRampToValueAtTime(0.25, ctx.currentTime + 0.01);
      g.gain.exponentialRampToValueAtTime(0.001, ctx.currentTime + 0.2);
      o.start(); o.stop(ctx.currentTime + 0.2);
    };
    wrongRef.current = () => {
      if (mute || !ctx || !ctx.createOscillator) return;
      const o = ctx.createOscillator();
      const g = ctx.createGain();
      o.connect(g); g.connect(ctx.destination);
      o.type = "square"; o.frequency.setValueAtTime(220, ctx.currentTime);
      g.gain.setValueAtTime(0.001, ctx.currentTime);
      g.gain.exponentialRampToValueAtTime(0.2, ctx.currentTime + 0.01);
      g.gain.exponentialRampToValueAtTime(0.001, ctx.currentTime + 0.25);
      o.start(); o.stop(ctx.currentTime + 0.25);
    };
  }, [mute]);
  return {
    playCorrect: () => correctRef.current && correctRef.current(),
    playWrong: () => wrongRef.current && wrongRef.current(),
  };
}

/******************** Copy Deck ********************/
function pickPraise() {
  const list = [
    "Bravo! 🌟",
    "Excellent travail! 🎉",
    "Tu es fort en maths! 💪",
    "Super! Continue comme ça! 🥳",
    "Fantastique! 🌈",
    "Génial! 👏",
  ];
  return list[randInt(0, list.length - 1)];
}

function pickNudge() {
  const list = [
    "Presque! Essaie encore! 💡",
    "Oups! Réessaye! 😊",
    "Tu y es presque! 🙌",
    "Courage, tu peux le faire! 💪",
  ];
  return list[randInt(0, list.length - 1)];
}

/******************** Main Component ********************/
export default function MultiplicationTrainer() {
  // Profile + settings
  const [kidName, setKidName] = useLocalStorageState(LS_KEYS.kidName, "");
  const [mode, setMode] = useLocalStorageState(LS_KEYS.mode, "Choix multiple"); // "Choix multiple" | "Saisie manuelle"
  const [mute, setMute] = useLocalStorageState(LS_KEYS.mute, false);

  // Game state
  const [streak, setStreak] = useLocalStorageState(LS_KEYS.streak, 0);
  const [{ a, b }, setPair] = useState(generatePair());
  const [inputValue, setInputValue] = useState("");
  const [feedback, setFeedback] = useState(null); // {type: 'ok'|'no', msg}
  const [celebrate, setCelebrate] = useState(false);
  const [showSettings, setShowSettings] = useState(!kidName);

  const correct = a * b;
  const wrongs = useMemo(() => pickWrongAnswers(correct, 2), [a, b]);
  const options = useMemo(() => {
    const arr = [correct, ...wrongs];
    // Fisher–Yates shuffle
    for (let i = arr.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [arr[i], arr[j]] = [arr[j], arr[i]];
    }
    return arr;
  }, [correct, wrongs]);

  const { playCorrect, playWrong } = useSound(mute);

  function nextQuestion() {
    setPair(generatePair());
    setInputValue("");
  }

  function handleResult(isCorrect) {
    if (isCorrect) {
      setFeedback({ type: "ok", msg: pickPraise() });
      playCorrect();
      const newStreak = streak + 1;
      setStreak(newStreak);
      if (newStreak >= 10) setCelebrate(true);
      setTimeout(() => { setFeedback(null); nextQuestion(); }, 700);
    } else {
      setFeedback({ type: "no", msg: pickNudge() });
      playWrong();
      setStreak(0);
      setTimeout(() => { setFeedback(null); nextQuestion(); }, 900);
    }
  }

  function submitInput() {
    if (inputValue.trim() === "") return;
    const val = Number(inputValue.trim());
    handleResult(val === correct);
  }

  // Keyboard: Enter validates in manual mode
  useEffect(() => {
    function onKey(e) {
      if (mode === "Saisie manuelle" && (e.key === "Enter" || e.key === "Return")) submitInput();
    }
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, [mode, inputValue, correct]);

  // Settings screen first if no kid name
  if (showSettings) {
    return (
      <div className="min-h-screen flex flex-col justify-center items-center bg-gradient-to-b from-blue-50 via-teal-50 to-yellow-100 p-4">
        <div className="bg-white/90 backdrop-blur-lg p-8 rounded-3xl shadow-xl w-full max-w-md text-center border border-blue-100">
          <h2 className="text-3xl font-extrabold text-blue-600 mb-2">👋 Bonjour!</h2>
          <p className="text-gray-600 mb-4">Entre ton prénom et choisis tes préférences pour commencer.</p>

          <div className="space-y-3">
            <input
              value={kidName}
              onChange={(e) => setKidName(e.target.value)}
              placeholder="Ton prénom"
              className="w-full rounded-2xl border border-blue-300 bg-blue-50 p-4 text-center text-xl font-semibold focus:border-blue-400 focus:ring-2 focus:ring-blue-200"
            />
            <div className="flex items-center justify-between">
              <span className="font-medium">Son</span>
              <button onClick={() => setMute((m) => !m)} className={`px-3 py-1 rounded-full text-sm font-semibold ${mute ? "bg-gray-200 text-gray-700" : "bg-emerald-500 text-white"}`}>
                {mute ? "Désactivé" : "Activé"}
              </button>
            </div>
            <div className="flex items-center justify-between">
              <span className="font-medium">Type de question</span>
              <button onClick={() => setMode(mode === "Choix multiple" ? "Saisie manuelle" : "Choix multiple")} className={`px-3 py-1 rounded-full text-sm font-semibold ${mode === "Choix multiple" ? "bg-blue-500 text-white" : "bg-gray-200 text-gray-700"}`}>
                {mode === "Choix multiple" ? "Choix (3)" : "Manuel"}
              </button>
            </div>
          </div>

          <button
            onClick={() => setShowSettings(false)}
            className="mt-5 rounded-full bg-blue-500 px-6 py-3 text-white font-semibold hover:bg-blue-600 transition disabled:opacity-50"
            disabled={!kidName.trim()}
          >
            Commencer 🚀
          </button>
        </div>
      </div>
    );
  }

  // Main UI
  return (
    <div className="min-h-screen w-full bg-gradient-to-b from-blue-50 via-teal-50 to-yellow-100 flex flex-col items-center justify-start p-4">
      <div className="mt-6 w-full max-w-3xl text-center">
        <h1 className="text-4xl font-extrabold text-transparent bg-clip-text bg-gradient-to-r from-blue-500 via-teal-400 to-yellow-400 drop-shadow-sm mb-3">
          🧮 Bonjour{kidName ? `, ${kidName}!` : "!"}
        </h1>
        <div className="flex justify-center gap-3">
          <button
            className={`rounded-full px-5 py-2 text-sm font-semibold shadow-lg transition-all hover:scale-105 ${mute ? "bg-gray-200 text-gray-700" : "bg-teal-500 text-white"}`}
            onClick={() => setMute((m) => !m)}
          >
            {mute ? "🔇 Son désactivé" : "🔊 Son activé"}
          </button>
          <button
            className={`rounded-full px-5 py-2 text-sm font-semibold shadow-lg transition-all hover:scale-105 ${mode === "Choix multiple" ? "bg-blue-500 text-white" : "bg-gray-200 text-gray-700"}`}
            onClick={() => setMode(mode === "Choix multiple" ? "Saisie manuelle" : "Choix multiple")}
          >
            {mode === "Choix multiple" ? "🎯 Mode: Choix (3)" : "⌨️ Mode: Manuel"}
          </button>
          <button
            onClick={() => setStreak(0)}
            className="rounded-full px-5 py-2 text-sm font-semibold shadow-lg transition-all hover:scale-105 bg-amber-500 text-white"
          >
            Réinitialiser la série
          </button>
          <button
            onClick={() => setShowSettings(true)}
            className="rounded-full px-5 py-2 text-sm font-semibold shadow-lg transition-all hover:scale-105 bg-white text-blue-700 border border-blue-200"
          >
            ⚙️ Paramètres
          </button>
        </div>
      </div>

      <div className="mt-6 w-full flex flex-col items-center gap-3">
        <ProgressBar value={streak} max={10} />
        <div className="mt-4 w-full max-w-md rounded-3xl bg-white/80 backdrop-blur p-8 shadow-xl border border-blue-100">
          <div className="mb-4 text-center text-5xl font-extrabold text-blue-700">
            {a} × {b} = ?
          </div>

          {mode === "Choix multiple" ? (
            <div className="grid grid-cols-3 gap-3">
              {options.map((opt) => (
                <button
                  key={opt}
                  onClick={() => handleResult(opt === correct)}
                  className="rounded-2xl bg-gradient-to-br from-blue-100 via-teal-100 to-yellow-100 p-5 text-3xl font-bold text-gray-800 shadow-lg hover:scale-105 hover:shadow-xl transition"
                >
                  {opt}
                </button>
              ))}
            </div>
          ) : (
            <div className="flex flex-col items-center gap-3">
              <input
                inputMode="numeric"
                pattern="[0-9]*"
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value.replace(/[^0-9]/g, ""))}
                className="w-full max-w-xs rounded-2xl border border-blue-300 bg-blue-50 p-4 text-center text-3xl font-bold outline-none focus:border-blue-400 focus:ring-2 focus:ring-blue-200"
                placeholder="Ta réponse ici"
              />
              <button
                onClick={submitInput}
                className="w-full max-w-xs rounded-2xl bg-blue-500 p-4 text-lg font-semibold text-white shadow-lg transition hover:scale-105 hover:bg-blue-600"
              >
                Vérifier
              </button>
            </div>
          )}

          <div className="mt-4 min-h-[2.25rem] text-center text-xl font-semibold">
            {feedback && (
              <span className={feedback.type === "ok" ? "text-emerald-600" : "text-red-600"}>{feedback.msg}</span>
            )}
          </div>
        </div>
      </div>

      {celebrate && (
        <div className="fixed inset-0 z-40 flex items-center justify-center bg-white/80 p-6 text-center">
          <div className="max-w-md rounded-3xl bg-white p-8 shadow-2xl">
            <div className="mb-2 text-5xl">🎊</div>
            <h2 className="mb-2 text-2xl font-extrabold text-blue-600">Bravo, {kidName || "champion"}! 🎉</h2>
            <p className="mb-5 text-gray-700">Tu as eu 10 bonnes réponses d'affilée! 🏅</p>
            <button
              onClick={() => { setStreak(0); setCelebrate(false); nextQuestion(); }}
              className="rounded-xl bg-blue-500 px-5 py-3 text-sm font-semibold text-white shadow hover:bg-blue-600"
            >
              Rejouer
            </button>
          </div>
          <Confetti fire={true} />
        </div>
      )}
    </div>
  );
}

/******************** Dev Self-Tests (console) ********************/
function runSelfTests() {
  try {
    // randInt bounds
    for (let i = 0; i < 200; i++) {
      const v = randInt(2, 5);
      console.assert(v >= 2 && v <= 5, "randInt out of bounds", v);
    }
    // generatePair in [0..9]
    for (let i = 0; i < 50; i++) {
      const { a, b } = generatePair();
      console.assert(a >= 0 && a <= 9 && b >= 0 && b <= 9, "generatePair out of range", a, b);
    }
    // wrong answers unique & != correct
    for (let c = 0; c <= 81; c += 9) {
      const ws = pickWrongAnswers(c, 2);
      console.assert(ws.length === 2, "wrong count != 2", ws);
      console.assert(new Set(ws).size === 2, "wrong not unique", ws);
      console.assert(!ws.includes(c), "wrong includes correct", ws, c);
      console.assert(ws.every((x) => x >= 0 && x <= 81), "wrong out of bounds", ws);
    }
    // options correctness
    for (let i = 0; i < 20; i++) {
      const a = randInt(0, 9), b = randInt(0, 9);
      const correct = a * b;
      const opts = [correct, ...pickWrongAnswers(correct, 2)];
      console.assert(opts.length === 3, "options length != 3", opts);
      console.assert(opts.includes(correct), "options missing correct", opts, correct);
    }
    console.log("[MulTrainer] Self-tests passed ✅");
  } catch (e) {
    console.error("[MulTrainer] Self-tests failed ❌", e);
  }
}

if (typeof window !== "undefined" && !window.__multrainerTested) {
  window.__multrainerTested = true;
  runSelfTests();
}
